<template>
  <line-chart :chart-data="readDataset"></line-chart>
  <line-chart :chart-data="writeDataset"></line-chart>
</template>

<script>
import LineChart from '@/components/modules/LineChart.vue'

export default {
  name: 'dashboard',
  components: {
    LineChart
  },
  data: () => ({
    readDataset: null,
    writeDataset: null,
    readData: null,
    writeData: null,
    socket: null
  }),
  mounted () {
    const socket = new WebSocket(`ws://${window.location.host}/ws`)
    this.$data.socket = socket
    this.$data.readData = new Map([])
    this.$data.writeData = new Map([])
    socket.onmessage = e => {
      const data = JSON.parse(e.data)
      data.forEach((value) => {
        this.createReadData(value)
        this.createWriteData(value)
      })
      console.log(this)
    }
  },
  methods: {
    createReadData(data) {
      if (this.readData[data.file_name]) {
        this.readData[data.file_name].push(data.read_bytes_per_sec)
      }else {
        this.readData[data.file_name] = [data.read_bytes_per_sec]
      }
    },
    createWriteData(data) {
      if (this.writeData[data.file_name]) {
        this.writeData[data.file_name].push(data.write_bytes_per_sec)
      }else {
        this.writeData[data.file_name] = [data.write_bytes_per_sec]
      }
    },
    createReadDataset (data) {
      if (this.readDataset === null) {
        this.readDataset = {
          labels: [],
          datasets: []
        }
        data.forEach((value) => {
          console.log(value)
        })
      }
    },
    createWriteDataset (data) {
    }
  },
  destoryed () {
    this.$data.socket.close()
  }
}
</script>



